body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.memberPage {
    background-color: rgb(32, 211, 218);
}

header {
    font-size: 50px;
    text-align: center;
    padding: 15px;
    font-style: italic;
}

button {
    font-size: 25px;
    color: black;
    padding: 8px;
    margin-bottom: 10px;
    margin-left: 80%;
    margin-right: 0px;
}

.SavedQuilts {
    text-align: center;
    outline-style: solid;
    padding: 10px;
    width: 45%;
    float: left;
    background-color: lightgray;
    margin-bottom: 10px;
    margin-left: 15px;
    height: 450px;
}

.LargerView {
    margin-right: 15px;
    padding: 10px;
    outline-style: solid;
    width: 45%;
    height: 800px;
    float: right;
    background-color: lightgray;
    text-align: center;
}

